import React, { Component, Suspense } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment'
import { baseUrl } from '../../../../Assets/AssetsHelper';
import ProfileImage from '../../CommonComponents/ProfileImage/ProfileImage';
import { GET_POST_TYPE_NAME, GET_MEDIA_NAME, MEDIA_REPLACE_URL, GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../Services/Utils/utils'
import PerfectScrollbar from 'react-perfect-scrollbar'


class RecentPosts extends Component {

    mediaType(recentItem) {
        switch (recentItem.type) {
            case 1:
                return <div className="block-center flex-column text-gray-800 text-center fs-10 fw-medium">
                    {recentItem.rating > 0 ?
                        <>
                            <i className="ri-star-fill icon-6" style={{color:'#FBBB01',marginBottom:2}}></i>
                            4/5
                        </>
                        :
                        <i className="ri-text icon-5"></i>
                    }
                </div>
            case 2:
            case 3:
            case 4:
            case 5:
                return <img className="object-center-cover" src={recentItem.postFile} height={44} width={44} alt={GET_MEDIA_NAME(recentItem.postFile)} onError={event => event.target.style = "display:none;"} />
            default:
                return false;
        }
    }
    render() {
        const { recentPost } = this.props;
        return <div className="table-responsive assets_table">
            <PerfectScrollbar options={{ wheelPropagation: false }}>
                <table className="table table-md align-middle gs-0 gy-3 my-0">
                    <thead className="thead-light">
                        <tr>
                            <th className="ps-0" style={{ minWidth: 50 }}><span className="fs-8 text-uppercase fw-bold text-nowrap text-gray-500">Assets</span></th>
                            <th style={{ minWidth: 150 }}><span className="fs-8 text-uppercase fw-bold text-nowrap text-gray-500">Author</span></th>
                            <th style={{ minWidth: 90 }}><span className="fs-8 text-uppercase fw-bold text-nowrap text-gray-500">Created</span></th>
                            <th style={{ minWidth: 50 }}><span className="fs-8 text-uppercase fw-bold text-nowrap text-gray-500">Type</span></th>
                            <th style={{ minWidth: 90 }}><span className="fs-8 text-uppercase fw-bold text-nowrap text-gray-500">Collection</span></th>
                            <th className="pe-0" style={{ minWidth: 50 }}><span className="fs-8 text-uppercase fw-bold text-nowrap text-gray-500">Network</span></th>
                        </tr>
                    </thead>
                    <tbody>
                        {recentPost.map((recentItem, index) => {
                            return <tr key={`${recentItem.id}_${index}`} data-id={recentItem.id}>
                                <td className="ps-0">
                                    <Link to={`/assets?assetId=${recentItem.id}`} className="cursor-pointer text-decoration-none">
                                        <div className="d-flex align-items-center">
                                            <div className="symbol symbol-38 d-flex align-items-center justify-content-center flex-shrink-0 bg-light">
                                                {this.mediaType(recentItem)}
                                            </div>
                                        </div>
                                    </Link>
                                </td>
                                <td>
                                    <a href={recentItem.social && recentItem.social.id != 0 && recentItem.social.id != 29 ? recentItem.link : recentItem.postFile} target={recentItem.social && recentItem.social.id != 0 && recentItem.social.id != 29 ? '_blank' : '_blank'} className="d-flex align-items-center cursor-pointer text-decoration-none">
                                        <ProfileImage className={'symbol symbol-38 rounded-circle bg-light flex-shrink-0 me-2 overflow-hidden'}
                                            imageSize={40}
                                            symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-5'}
                                            name={recentItem.name} profileUrl={MEDIA_REPLACE_URL(recentItem.picture)} networkId={recentItem.social && recentItem.social ? recentItem.social.id : 0} />
                                        <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                                            <p className="text-gray-dark fw-semibold mb-0 text-ellipsis">{recentItem.name}</p>
                                            <span className="fs-9 mb-0 text-gray-500 fw-medium text-ellipsis d-block">@{recentItem.username}</span>
                                        </div>
                                    </a>
                                </td>
                                <td>
                                    <p className="text-gray-dark fw-semibold mb-0 text-nowrap">{moment(recentItem.created).format("DD MMM YYYY")}</p>
                                    <span className="fs-9 fw-medium text-gray-600 text-nowrap">{moment(recentItem.created).format("hh:m a")}</span>
                                </td>
                                <td>
                                    <span className="text-gray-dark fw-medium mb-0 text-nowrap">{GET_POST_TYPE_NAME(recentItem.type)}</span>
                                </td>
                                <td>
                                    <div className="overflow-hidden" style={{ maxWidth: 120 }}>
                                        <span className="text-gray-dark fw-medium mb-0 line-clamp line-clamp-2 text-break">{recentItem.collection ? recentItem.collection : '--'}</span>
                                    </div>
                                </td>
                                <td className="pe-0 text-center">
                                    <a href={recentItem.social && recentItem.social.id != 0 && recentItem.social.id != 29 ? recentItem.link : recentItem.postFile} target={recentItem.social && recentItem.social.id != 0 && recentItem.social.id != 29 ? '_blank' : '_blank'} className="w-100 text-decoration-none block-center cursor-pointer">
                                        <span className="h-20px w-20px">
                                            <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(recentItem.social ? recentItem.social.id : 0)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(recentItem.social ? recentItem.social.id : 0)} />
                                        </span>
                                    </a>
                                </td>
                            </tr>
                        })}
                    </tbody>
                </table>
            </PerfectScrollbar>
        </div>
    }
}

export default RecentPosts;